<script>
import { mapActions } from 'vuex';
import axios from 'axios';
import feather from "feather-icons";
import "datatables.net-bs5";
import "datatables.net-bs5/css/dataTables.bootstrap5.min.css";

export default {
    props: ['id'],
    data() {
        return {
            dataTable: null,
            selectedFile: null,
            showModal: false,
            expediente: {},
            escritos: [],
            ejes: [],
            judicial: {},
            sector: '',
            procesal: '',
            EJE: {
                name: "",
                exp_id: "",
                doc_tipo: "EJE",
                descripcion: ""
            },
            archivoSeleccionado: false,
        };
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    async created() {
        this.sector = this.$store.getters.get__sector;
        this.cargarDetallesExpediente();
        this.dataTable = null;
    },
    methods: {
        ...mapActions(['get', 'post']),
        mostrarModal() {
            this.showModal = true;
        },

        cerrarModal() {
            this.showModal = false;
            this.limpiarCampos();
            this.limpiarErrores();
        },


        async cargarDetallesExpediente() {
            try {
                const response = await this.get({
                    url: `${this.$store.getters.get__url}/proceeding/${this.id}`,
                    token: this.$store.getters.get__token,
                });
                this.expediente = response.data;
                this.escritos = response.escritos;
                console.log(this.escritos);
                this.ejes = response.eje;
                this.procesal = response.procesal;
                this.judicial = response.judicial;
                this.EJE.exp_id = response.data.exp_id;
                this.initDataTable();
                console.log(this.EJE.exp_id);
                feather.replace();
            } catch (error) {
                console.error('Error al cargar los detalles del expediente:', error);
            }
        },
        formatFecha(fechaISO) {
            const fecha = new Date(fechaISO);
            const opcionesFormato = {
                year: 'numeric',
                month: 'short',
                day: 'numeric',
                hour: 'numeric',
                minute: 'numeric',
                second: 'numeric'
            };
            return fecha.toLocaleDateString('es-ES', opcionesFormato);
        },
        traerarchivo: function (name) {
            axios
                .get(this.$store.getters.get__url + '/traer/archivo', {
                    responseType: 'blob',
                    headers: {
                        Authorization: 'Bearer ' + this.$store.getters.get__token,
                    },
                    params: {
                        nombre: name,
                    },
                })
                .then((response) => {
                    const blob = new Blob([response.data], {
                        type: response.headers['content-type'],
                    });
                    const url = window.URL.createObjectURL(blob);
                    window.open(url, '_blank');
                    window.URL.revokeObjectURL(url);
                })
                .catch((error) => {
                    console.error('Error al abrir el archivo', error);
                });
        },
        goBack() {
            this.$router.go(-1);
        },
        async editarEje() {
            if (this.archivoSeleccionado) {
                try {
                    const formData = new FormData();
                    formData.append('file', this.selectedFile);

                    await axios.post(
                        `${this.$store.getters.get__url}/cargar/archivo/eje`,
                        formData,
                        {
                            headers: {
                                'Content-Type': 'multipart/form-data',
                                Authorization: 'Bearer ' + this.$store.getters.get__token,
                            },
                            params: this.EJE,
                        }
                    );
                    console.info('Carga correcta del archivo eje.');
                    this.selectedFile = null;
                    this.modalVisible = false;
                    this.cargarDetallesExpediente();
                } catch (error) {
                    console.error(error);
                }
            } else {
                console.log('Falta cargar un archivo.');
            }
        },

        onFileChange(event) {
            this.selectedFile = event.target.files[0];
            const fileInput = event.target;
            this.archivoSeleccionado = fileInput.files.length > 0;
        },

        initDataTable() {
            this.$nextTick(() => {
                // Destruye DataTable si ya existe
                if (this.dataTable) {
                    this.dataTable.destroy();
                }

                // Inicializa DataTable
                this.dataTable = $("#table-escritos").DataTable({
                    language: {
                        url: "//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json",
                    },
                });

                // Reemplaza iconos Feather
                feather.replace();
            });
        },

    },

    beforeDestroy() {
        if (this.dataTable) {
            this.dataTable.destroy();
        }
    },
};
</script>

